import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      listArr: [] //
    };
  },
  methods: {
    details(index, item) {
      this.$router.push({
        path: "findListDetails",
        query: {
          id: item.id
        }
      });
    },

    getList() {
      this.$vux.loading.show({
        text: "Loading"
      });
      if(this.$route.query.name == '常用药品'){
        base
        .get("/api/app/common_drug/")
        .then(res => {
          this.$vux.loading.hide();
          console.log(res);
          if (res.data.status == 200001) {
            this.listArr = res.data.msg;
          } else {
            console.log("获取药品列表失败");
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
        });
      }else{//常用药品
          base.get('/api/app/recommend_drug/')
        .then(res=>{
          this.$vux.loading.hide();
          console.log(res,"获取药师推荐列表");
          if(res.data.status == 200001){
            this.listArr = res.data.msg;
          }else {
            console.log("药师推荐列表获取失败")
          }
        }).catch(err=>{
          this.$vux.loading.hide();
          console.log("药师推荐列表获取失败")
        })
      }
    }
  },
  mounted() {
    this.getList(); //获取列表
  }
};



