import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      textareaShow: false, //文本框显示隐藏
      focused: false,
      tetxarea: "",
      id: "", //新闻ID
      typeId: "", //类型id
      details: {}, //详情
      contentArr: [] //评论列表
    };
  },
  methods: {
    // 获取传值ID请求新闻详情
    getDetails() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/app/news/" + this.id + "/")
        .then(res => {
          console.log(res, "获取新闻详情");
          this.$vux.loading.hide();
          if (res.data.status == 200114) {
            this.details = res.data.msg;
          } else {
            console.log("获取新闻详情失败");
          }
        })
        .catch(err => {
          console.log(err);
          this.$vux.loading.hide();
        });
    },

    // 获取药物常识详情
    getKnowledgeDetails() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/app/drug_sense/" + this.id + "/")
        .then(res => {
          this.$vux.loading.hide();
          console.log(res, "获取药物常识详情");
          if (res.data.status == 200102) {
            this.details = res.data.msg;
          } else {
            console.log("获取药物常识失败");
          }
        })
        .catch(err => {
          console.log(err);
          this.$vux.loading.hide();
        });
    },

    // 获取评论列表
    getContentList() {
      if (this.$route.query.name == "行业新闻") {
        base
          .get(
            "/api/app/comment/?body_id=" +
              this.id +
              "&body_type=1&page=1&page_size=3"
          )
          .then(res => {
            console.log(res, "行业新闻评论列表");
            if (res.data.status == 200001) {
              this.contentArr = res.data.msg;
            } else {
              console.log("获取评论列表失败");
            }
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        base
          .get(
            "/api/app/comment/?body_id=" +
              this.id +
              "&body_type=2&page=1&page_size=3"
          )
          .then(res => {
            console.log(res, "药物常识评论列表");
            if (res.data.status == 200001) {
              this.contentArr = res.data.msg;
            } else {
              console.log("获取评论列表失败");
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },

    // 点击点赞小图标
    likeBtn() {
      this.$vux.loading.show({
        text: "Loading"
      });
      if (this.$route.query.name == "行业新闻") {
        base
          .post("/api/app/upvote/news/" + this.id + "/")
          .then(res => {
            console.log(res);
            this.$vux.loading.hide();
            if (res.data.status == 200115) {
              this.$vux.toast.show({
                text: res.data.desc
              });
              this.getDetails();
            } else {
              this.$vux.toast.show({
                type: "text",
                text: res.data.desc,
                isShowMask: true
              });
            }
          })
          .catch(err => {
            this.$vux.loading.hide();
            console.log(err);
          });
      } else {
        base
          .post("/api/app/upvote/drug_sense/" + this.id + "/")
          .then(res => {
            console.log(res);
            this.$vux.loading.hide();
            if (res.data.status == 200115) {
              this.$vux.toast.show({
                text: res.data.desc
              });
              this.getKnowledgeDetails();
            } else {
              this.$vux.toast.show({
                type: "text",
                text: res.data.desc,
                isShowMask: true
              });
            }
          })
          .catch(err => {
            this.$vux.loading.hide();
            console.log(err);
          });
      }
    },

    // 取消点赞
    cancelLikeBtn() {},

    //   控制文本框的显示和隐藏
    inputFocus() {
      this.textareaShow = true;
      this.focused = true;
    },
    textareaH() {
      this.textareaShow = false;
    },
    textareaS() {
      this.textareaShow = true;
    },

    // 发表按钮点击事件
    submitBtn() {
      console.log("发表");
      if (
        this.tetxarea == "" ||
        this.tetxarea.split(" ").join("").length == 0
      ) {
        this.$vux.toast.show({
          type: "text",
          text: "评论不能为空"
        });
      } else {
        if (this.$route.query.name == "行业新闻") {
          var data = {
            body_id: this.id,
            body_type: 1,
            content: this.tetxarea
          };
          this.$vux.loading.show({
            text: "Loading"
          });
          base
            .post("/api/app/comment/", JSON.stringify(data))
            .then(res => {
              console.log(res);
              this.$vux.loading.hide();
              if (res.data.status == 200116) {
                this.$vux.toast.show({
                  text: res.data.desc
                });
                this.tetxarea = "";
                this.textareaShow = false;
                this.getContentList();
              } else {
                this.$vux.toast.show({
                  type: "text",
                  text: res.data.desc
                });
              }
            })
            .catch(err => {
              console.log(err);
              this.$vux.loading.hide();
            });
        } else {
          var data = {
            body_id: this.id,
            body_type: 2,
            content: this.tetxarea
          };
          this.$vux.loading.show({
            text: "Loading"
          });
          base
            .post("/api/app/comment/", JSON.stringify(data))
            .then(res => {
              console.log(res);
              this.$vux.loading.hide();
              if (res.data.status == 200116) {
                this.$vux.toast.show({
                  text: res.data.desc
                });
                this.textareaShow = false;
                this.getContentList();
              } else {
                this.$vux.toast.show({
                  type: "text",
                  text: res.data.desc
                });
              }
            })
            .catch(err => {
              console.log(err);
              this.$vux.loading.hide();
            });
        }
      }
    }
  },

  mounted() {
    console.log(this.$route);
    if (this.$route.query) {
      this.id = this.$route.query.id;
      this.getContentList(); //获取评论列表
      if (this.$route.query.name == "行业新闻") {
        this.getDetails();
      } else if (this.$route.query.name == "药物常识") {
        this.getKnowledgeDetails();
      }
    }
  },

  directives: {
    focus: {
      inserted: function(el, { value }) {
        if (value) {
          el.focus();
        }
      }
    }
  }
};



