import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      detailsObj: {},
      id: "" //医院id
    };
  },
  methods: {
    getDetails() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/app/hospital/" + this.id + "/")
        .then(res => {
          this.$vux.loading.hide();
          console.log(res);
          if (res.data.status == 200103) {
            this.detailsObj = res.data.msg;
          } else {
            console.log("获取医院详情失败");
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
        });
    }
  },
  mounted() {
    if (this.$route.query) {
      this.id = this.$route.query.id;
      this.getDetails();
    }
  }
};



