import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      answerList: [], //
      doctorId: "" //
    };
  },
  methods: {
    // 获取问答列表
    getAnswerList() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/app/doctor_answer/?doctor_id=" + this.doctorId)
        .then(res => {
          console.log(res);
          this.$vux.loading.hide();
          if (res.data.status == 200001) {
            this.answerList = res.data.msg;
          } else {
            console.log("获取列表失败");
          }
        })
        .catch(err => {
          console.log(err);
          this.$vux.loading.hide();
        });
    },

    // 点击进入详情页
    details(index, item) {
      this.$router.push({
        path: "newProblemDetails"
      });
    }
  },
  mounted() {
    if (this.$route.query) {
      this.doctorId = this.$route.query.id;
      this.getAnswerList(); //
    }
  }
};



