
import base from "../../components/request/baseUrl.js";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
data() {
return {

bannersArr: [],
tabs: [
{ name: "国内动态", id: "1" },
{ name: "国际前沿", id: "2" },
{ name: "医院新闻", id: "3" }
], //tab列表
num: 0, //默认tab显示索引值
activeId: 1, //请求列表的第几项
newsArr: [] 
};
},
methods: {

selectBtn(index) {
this.num = index - 1;
this.activeId = index;
this.getNewsList();
},


details(index, item) {


localStorage.setItem("homeNewsTabNum", this.num);
this.$router.push({
path: "homeNewsDetails",
query: {
id: item.id,
name: "行业新闻"
}
});
},


getBanners() {
this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/app/advertise/?ad_where=news")
.then(res => {
console.log(res);
this.$vux.loading.hide();
if (res.data.status == 200101) {
this.bannersArr = res.data.msg;
} else {
console.log("获取轮播图失败");
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},


getNewsList() {
this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/app/news/?news_type=" + this.activeId)
.then(res => {
console.log(res, "获取行业新闻列表");
this.$vux.loading.hide();
if (res.data.status == 200001) {
this.newsArr = res.data.msg;
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
}
},
beforeRouteEnter(to, from, next) {

next(vm => {
if (from.path == "/homeNewsDetails") {
console.log(vm.num)
vm.num = localStorage.getItem("homeNewsTabNum");
vm.activeId = Number(localStorage.getItem("homeNewsTabNum")) + 1;
vm.getNewsList();
} else if (from.path == "/home") {
if (JSON.stringify(vm.$route.query) == "{}") {
vm.activeId = 1;
vm.num = 0;
} else {
vm.activeId = Number(vm.$route.query.id);
vm.num = Number(vm.$route.query.id) - 1;
}
vm.getNewsList();
}else if(from.path == null || from.path == '' || from.path == undefined || from.path == '/'){
if (JSON.stringify(vm.$route.query) == "{}") {
vm.activeId = 1;
vm.num = 0;
} else {
vm.activeId = Number(vm.$route.query.id);
vm.num = Number(vm.$route.query.id) - 1;
}
vm.getNewsList();
}
});
},
mounted() {
this.getBanners(); 

},
components: {
Tab,
TabItem,
Swiper,
SwiperItem
}
};



