
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import base from "../../components/request/baseUrl.js";
export default {
data() {
return {
msg: "Welcome to Your Vue.js App",
search: "", //搜索内容
hotList: [],
listArr: [], //渲染出来的列表
historyList: [], //历史记录
noneList: true,       haveList: false,       nothing: false,
scrolled: false
};
},
methods: {

selectHotBtn(index, item) {
console.log(item);
this.$router.push({
path: "findSearchList",
query: {
keyword: item.keyword
}
});
this.historyList = this.historyList.filter(v => v != item.keyword);
this.historyList.unshift(item.keyword);
localStorage.setItem("findProblemHistory", JSON.stringify(this.historyList));
},


selectHisBtn(index, item){
this.search = item.keyword;
this.searchBtn();
},




searchBtn(index, item) {
if (this.search == "" || this.search.split(" ").join("").length == 0) {
return;
} else {
this.historyList = this.historyList.filter(v => v != this.search);
this.historyList.unshift(this.search);
localStorage.setItem("findProblemHistory", JSON.stringify(this.historyList));
this.$router.push({
path: "findSearchList",
query: {
keyword: this.search
}
});
}
},




getHotList() {
this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/app/hot_search_key/drug/")
.then(res => {
this.$vux.loading.hide();
console.log(res);
if (res.data.status == 200001) {
this.hotList = res.data.msg;
} else {
console.log("获取失败");
}
})
.catch(err => {
this.$vux.loading.hide();
consoel.log(err);
});
},




delHistoryItem(index,item){
console.log(this.historyList)
this.historyList.splice(index, 1);
var tempArr = this.historyList;
localStorage.setItem("findProblemHistory", JSON.stringify(tempArr));
},


delHistoryAll(){
console.log(123)
localStorage.removeItem("findProblemHistory");
this.historyList = [];
},


itemDetailsBtn(index, item) {
this.$router.push({
path: "findListDetails",
query: {
id: item.id
}
});
}
},

mounted() {
console.log();
if (JSON.parse(localStorage.getItem("listArr")) != null) {
this.listArr = JSON.parse(localStorage.getItem("listArr"));
}
this.getHotList();

if (JSON.parse(localStorage.getItem("findProblemHistory")) == null) {
this.historyList = [];
} else {
this.historyList = JSON.parse(localStorage.getItem("findProblemHistory"));
}
},
components: {
Tabbar,
TabbarItem
}
};



