
import base from "../../components/request/baseUrl.js";
import lrz from "lrz";
import Datetime from 'vux/src/components/datetime/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
data() {
return {
userInfo: {}, //用户信息
avatar: "",
birthday: "", //生日
sex: "", //性别
realName: "", //真实姓名
nickName: "", //昵称
area: "", //所在地
nickNameShow: false,
userId: "",
sexShow: false, //性别弹窗显示隐藏
sexArr: [{ sex: "男" }, { sex: "女" }], //性别数组
dateShow: false, //控制事件选择显示隐藏
addressData: ChinaAddressData,
addressValue: ["广东省", "广州市"],
tempAddressArr: [],
tempImgsArr: [],

maxYear: "",
};
},
methods: {

uploadImg() {
document.getElementById("uploadimg").click();
},


selectImg(e) {
var that = this;
var file = e.target.files[0];

if (!/image\/\w+/.test(file.type)) {
that.$vux.toast.show({
text: "请确认上传的类型为图片"
});
return false;
};
console.log(file);
lrz(file)
.then(function(rst) {

that.$vux.loading.show({
text: "Loading"
});
base
.post("/api/app/file/", {
file_name: rst.origin.name,
coding: "base64",
file: rst.base64,
classify: "user_avatar"
})
.then(res => {
that.$vux.loading.hide();
console.log(res);
if (res.data.status == 299001) {
var data = {
new_avatar: res.data.msg.url_path
};
base
.patch(
"/api/auth/user/profile/" + that.userId + "/",
JSON.stringify(data)
)
.then(res => {
console.log(res);
if (res.data.status == 201004) {
that.avatar = res.data.msg.avatar;
that.$vux.toast.show({
text: "更改成功"
});
} else {
that.$vux.toast.show({
text: res.data.desc
});
}
})
.catch(err => {
console.log(err);
});
}
})
.catch(err => {
that.$vux.loading.hide();
});
})
.catch(function(err) {

})
.always(function() {

});





















































},

editNickNamePop() {
this.nickNameShow = true;
},


editNickName() {

if (this.nickName == "") {
return;
} else {
var data = {
nickname: this.nickName
};
this.$vux.loading.show({
text: "Loading"
});
base
.patch(
"/api/auth/user/profile/" + this.userId + "/",
JSON.stringify(data)
)
.then(res => {
console.log(res);
this.$vux.loading.hide();
if (res.data.status == 201004) {
this.$vux.toast.show({
text: "修改成功"
});
} else {
this.$vux.toast.show({
type: "text",
text: "修改失败"
});
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
}
},


realnameBtn() {
this.$router.push({
path: "realname"
});
},


sexPopBtn() {
this.sexShow = true;
},


sexPopHide() {
this.sexShow = false;
},


sexPopShow() {
this.sexShow = true;
},


selectSex(index, item) {
var data = {
sex: item.sex
};
this.$vux.loading.show({
text: "Loading"
});
base
.patch(
"/api/auth/user/profile/" + this.userId + "/",
JSON.stringify(data)
)
.then(res => {
console.log(res);
this.$vux.loading.hide();
this.sexShow = false;
if (res.data.status == 201004) {
this.$vux.toast.show({
text: "修改成功"
});
this.sex = res.data.msg.sex;
} else {
this.$vux.toast.show({
type: "text",
text: "修改失败"
});
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},


dateBtn() {
this.dateShow = true;
},


selectDate(value) {
var data = {
birthday: value
};
this.$vux.loading.show({
text: "Loading"
});
base
.patch(
"/api/auth/user/profile/" + this.userId + "/",
JSON.stringify(data)
)
.then(res => {
console.log(res);
this.$vux.loading.hide();
this.sexShow = false;
if (res.data.status == 201004) {
this.$vux.toast.show({
text: "修改成功"
});
} else {
this.$vux.toast.show({
type: "text",
text: "修改失败"
});
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},

pickerAddress(id, name) {
this.tempAddressArr = name;
},


editAddress(confirm) {
if (confirm == true) {
var data = {
province: this.tempAddressArr[0],
city: this.tempAddressArr[1]
};
base
.patch(
"/api/auth/user/profile/" + this.userId + "/",
JSON.stringify(data)
)
.then(res => {
console.log(res);
if (res.data.status == 201004) {
this.$vux.toast.show({
text: "修改成功"
});
} else {
this.$vux.toast.show({
type: "text",
text: res.data.desc
});
}
})
.catch(err => {
console.log(err);
});
}
},

getUserInfo() {
this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/auth/user/profile/")
.then(res => {
console.log(res, "获取用户信息");
this.$vux.loading.hide();
if (res.data.status == 201002) {
if (
res.data.msg.id == undefined ||
res.data.msg.id == "" ||
res.data.msg.id == null
) {
this.userId = 2;
} else {
this.userId = res.data.msg.id;
}
if (
res.data.msg.avatar == undefined ||
res.data.msg.avatar == "" ||
res.data.msg.avatar == null
) {
this.avatar = require("../../assets/advisory-bg-photo-.png");
} else {
this.avatar = res.data.msg.avatar;
}
if (
res.data.msg.nickname == undefined ||
res.data.msg.nickname == "" ||
res.data.msg.nickname == null
) {
this.nickName = "昵称";
} else {
this.nickName = res.data.msg.nickname;
}
if (
res.data.msg.real_name == undefined ||
res.data.msg.real_name == "" ||
res.data.msg.real_name == null
) {
this.realName = "未实名";
} else {
this.realName = res.data.msg.real_name;
}
if (
res.data.msg.birthday == undefined ||
res.data.msg.birthday == "" ||
res.data.msg.birthday == null
) {
this.birthday = "未填写";
} else {
this.birthday = res.data.msg.birthday;
}
if (
res.data.msg.sex == undefined ||
res.data.msg.sex == "" ||
res.data.msg.sex == null
) {
this.sex = "未设置";
} else {
this.sex = res.data.msg.sex;
}
if (
res.data.msg.city == undefined ||
res.data.msg.city == "" ||
res.data.msg.city == null
) {
this.area = "未设置";
} else {
this.addressValue.splice(
0,
2,
res.data.msg.province,
res.data.msg.city
);
}
} else {
console.log("获取信息失败");
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
}
},
mounted() {
this.userInfo = this.$store.state.userInfo;
this.getUserInfo();



var date = new Date();
var year = date.getFullYear();
var month = date.getMonth()+1;
var day = date.getDate();
if(month<10){
month = "0" + month;
};
if(day<10){
day = "0" + day;
}
this.maxYear = year + '-' + month + '-' + day


},
components: {
Datetime,
Flexbox,
FlexboxItem,
Confirm,
Group,
Cell,
XAddress,
PopupPicker
}
};



