import Vue from 'vue';
import Vuex from 'vuex';
import base from '../components/request/baseUrl.js';

Vue.use(Vuex);
const store = new Vuex.Store({
    state: {
        tabbarShow: true,
        userInfo: {}
    },
    mutations: {
        showTabar(state, num) {
            state.tabbarShow = num;
        },
        hideTabar(state, num) {
            state.tabbarShow = num;
        },

        getUserInfo(state, data) {
            state.userInfo = data;
        }
    },
    actions: {
        showTabar(context, data) {
            context.commit("showTabar", data)
        },
        hideTabar(context, data) {
            context.commit("hideTabar", data)
        },

        // 用户信息
        getUserInfo(context, data) {
            // console.log(data)
            base.get('/api/auth/user/profile/')
                .then(res => {
                    console.log(res);
                    if (res.data.status == 201002) {
                        context.commit('getUserInfo', res.data.msg);
                    }
                }).catch(err => {
                    console.log(err);
                })
        }
    }
})

export default store


