import base from '../../components/request/baseUrl.js';
export default {
  data(){
    return{
      listArr: [],//收藏列表
    }
  },
  methods: {

    // 获取收藏列表
    getList(){
       this.$vux.loading.show({
        text: "Loading"
      });
      base.get('/api/app/favor_drug/')
      .then(res=>{
        this.$vux.loading.hide();
        console.log(res,"收藏列表");
        if(res.data.status == '200001'){
          this.listArr = res.data.msg;
        }else {
          console.log("获取收藏列表失败")
        }
      }).catch(err=>{
        this.$vux.loading.hide();
        console.log(err);
      })
    },

    detailsBtn(index,item){
      this.$router.push({
        path: 'findListDetails',
        query: {
          id: item.drug.id
        }
      })
    },


    // 点击用药提醒
    medicalTips(index,item){
      console.log(item)
      this.$router.push({
        path: 'myMedicalTips',
        query: {
          id: item.drug.id,
          name: item.drug.name
        }
      })
    }

  },
  mounted(){
    this.getList();//获取收藏列表
  }
};



