import base from '../../components/request/baseUrl.js';
export default {
    data(){
        return{
            hospitalArr: [],//医院列表
        }
    },
    methods: {
        details(index,item){
            this.$router.push({
                path: 'findHosDetails',
                query: {
                    id: item.id
                }
            })
        },


        // 获取医院列表
        getHospital(){
            this.$vux.loading.show({
                text: "Loading"
            });
            base.get('/api/app/hospital/')
            .then(res=>{
                console.log(res);
                this.$vux.loading.hide();
                if(res.data.status == 200001){
                    this.hospitalArr = res.data.msg;
                }else {
                    console.log("获取医院列表失败");
                }
            }).catch(err=>{
                console.log(err);
                this.$vux.loading.hide();
            })
        }
    },

    mounted(){
        this.getHospital();//获取医院列表
    }
}



