import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      questionId: "",
      detailsObj: {},
      doctorDetails: {},
      doctorAnswer: []
    };
  },
  methods: {
    
    //进入页面请求详情
    getQuestionDetais() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/app/search_consult/" + this.questionId + "/")
        .then(res => {
          console.log(res, "问题详情");
          this.$vux.loading.hide();
          if (res.data.status == 200107) {
            this.detailsObj = res.data.msg;
            this.doctorDetails = res.data.msg.doctor;
            this.doctorAnswer = res.data.msg.consult_answers;
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
        });
    }
  },
  mounted() {
    if (JSON.stringify(this.$route.query) == "{}") {
    } else {
      this.questionId = this.$route.query.questionId;
      this.getQuestionDetais(); //进入页面请求详情
    }
  }
};



