
import base from "../../components/request/baseUrl.js";
import lrz from "lrz";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
data() {
return {
placeholder:
"请简单描述病情\n例如:主要症状、发病时间、病情变化、想了解什么问题（不少于10字）",
msgShow: true,

doctorId: "",
doctorMsgObj: {}, //医生信息

textarea: "", //文本框
questionTitle: "", //问题title
imgsArr: [], //图片列表
sexArr: ["男", "女"],
imgsArrLength: "0",
imgOrder: 0,

userSex: "", //用户性别
birthday: "", //用户出生日期
realname: "", //用户真实姓名
userName: "", //昵称
userPhone: "", //用户手机号

selected: 0,
name: "",
age: "",
phone: "",       remnant: 200 //字数
};
},
methods: {
sexBtn_m(index, item) {
this.selected = index;
},

saveBtn() {
this.msgShow = true;
if (this.selected == 0) {
this.userSex = "男";
} else {
this.userSex = "女";
}
},


descInput() {
var txtVal = this.textarea.length;
this.remnant = 200 - txtVal;
},


getUserInfo() {
this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/auth/user/profile/")
.then(res => {
console.log(res, "获取用户信息");
this.$vux.loading.hide();

console.log(res.data.msg);
if (res.data.status == 201002) {



this.userPhone = res.data.msg.user.phone;
this.userSex = res.data.msg.sex;
this.userName = res.data.msg.nickname;
if (res.data.msg.birthday == "") {
this.birthday = 0;
} else {
this.birthday =
new Date().getFullYear() - res.data.msg.birthday.slice(0, 4);
}

} else {
console.log(res.data.desc);
}
})
.catch(err => {
console.log(err);
this.$vux.loading.hide();
});
},


getdoctorMsg() {
this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/app/doctor/" + this.doctorId + "/")
.then(res => {
console.log(res, "获取医生信息");
this.$vux.loading.hide();
if (res.data.status == 200104) {
this.doctorMsgObj = res.data.msg;
} else {
console.log(res.data.desc);
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},


modifyBtn() {
this.$router.push({
path: "homePatientModify",
query: {
doctorId: this.doctorId
}
});
},


details() {},


imgBtn() {
document.getElementById("fileInputUpload").click();
},


selectImg(e) {
if (this.imgsArrLength < 9) {
var that = this;
var file = e.target.files[0];

if (!/image\/\w+/.test(file.type)) {
that.$vux.toast.show({
text: "请确认上传的类型为图片"
});
return false;
}
lrz(file)
.then(function(rst) {

that.$vux.loading.show({
text: "Loading"
});
base
.post("/api/app/file/", {
file_name: rst.origin.name,
coding: "base64",
file: rst.base64,
classify: "user_consult"
})
.then(res => {
that.$vux.loading.hide();
console.log(res);
if (res.data.status == 299001) {
that.$vux.toast.show({
text: res.data.desc
});
that.imgsArr.push({
img_url: res.data.msg.url_path,
order: (that.imgOrder += 1)
});
that.imgsArrLength = that.imgsArr.length;
} else {
that.$vux.toast.show({
text: res.data.desc
});
}
})
.catch(err => {
that.$vux.loading.hide();
});
})
.catch(function(error) {

})
.always(function() {

});







































}
},


submitBtn() {
if (
this.userName == "" ||
this.userSex == "" ||
this.birthday == "" ||
this.userPhone == ""
) {
this.$vux.toast.show({
type: "text",
text: "请先完善患者信息"
});
} else if (this.questionTitle == "") {
this.$vux.toast.show({
type: "text",
text: "病情标题不能为空"
});
} else if (this.textarea == "") {
this.$vux.toast.show({
type: "text",
text: "病情描述不能为空"
});
} else if (this.textarea.length < 10) {
this.$vux.toast.show({
type: "text",
text: "病情描述不少于10字"
});
} else {
this.$vux.loading.show({
text: "Loading"
});
base
.post(
"/api/app/consult/",
JSON.stringify({
is_open: false,
patient_name: this.userName,
patient_sex: this.userSex,
patient_age: this.birthday,
patient_phone: this.userPhone,
question_title: this.questionTitle,
question_content: this.textarea,
image_consult: this.imgsArr,
doctor: {
id: this.doctorId
}
})
)
.then(res => {
console.log(res);
this.$vux.loading.hide();
if (res.data.status == 200106) {
this.$vux.toast.show({
text: res.data.desc
});
this.$router.push({
path: "homeConsultSuccess",
query: {
id: res.data.msg.consult_id
}
});
} else {
this.$vux.toast.show({
type: "text",
text: res.data.desc
});
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
}
},


getAllMsg() {
console.log(this.$route, "进入页面取值");
}
},
beforeRouteEnter(to, from, next) {
console.log(from, "判断是从哪个页面进入的");
next(vm => {
if (from.name == "doctorDetails" || from.name == "homeSelectRoom") {
vm.doctorId = localStorage.getItem("doctorId");
vm.getdoctorMsg(); 
vm.getUserInfo(); 
} else if (from.name == "homePatientModify") {
vm.doctorId = localStorage.getItem("doctorId");
console.log(vm.$route.query);
if (JSON.stringify(vm.$route.query) == "{}") {
vm.getUserInfo(); 
} else {
vm.userName = vm.$route.query.name;
vm.userSex = vm.$route.query.sex;
vm.birthday = vm.$route.query.age;
vm.userPhone = vm.$route.query.phone;
}
vm.getdoctorMsg(); 
} else if (
from.name == null ||
from.name == "" ||
from.name == undefined
) {
vm.doctorId = localStorage.getItem("doctorId");
vm.getdoctorMsg(); 
vm.getUserInfo(); 
} else {
vm.doctorId = localStorage.getItem("doctorId");
vm.getdoctorMsg(); 
vm.getUserInfo(); 
}
});
},
mounted() {},
components: {
Flexbox,
FlexboxItem
}
};



