import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      keyword: "",
      listArr: [],
      extraArr: []
    };
  },
  methods: {
    // 点击搜索
    searchBtn() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/app/search_consult/?keyword=" + this.keyword)
        .then(res => {
          console.log(res);
          this.$vux.loading.hide();
          if (res.data.status == 200001) {
            this.listArr = res.data.msg;
            this.extraArr = res.data.extra;
            this.changeColor(res.data.msg);
          } else {
            console.log("获取问题列表失败");
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
        });
    },

    changeColor(resultsList) {
      resultsList.map((item, index) => {
        // console.log('item', item)
        if (this.keyword && this.keyword.length > 0) {
          // 匹配关键字正则
          var reg = new RegExp(this.extraArr.join("|"), "g");
          item.question_content = item.question_content.replace(reg, function(
            word
          ) {
            return "<span style='color:#76A6EF;'>" + word + "</span>";
          });
          item.question_title = item.question_title.replace(reg, function(
            word
          ) {
            console.log(word);
            return "<span style='color:#76A6EF;'>" + word + "</span>";
          });
        }
      });
      this.listArr = [];
      this.listArr = resultsList;
    },

    details(index, item) {
      this.$router.push({
        path: "newProblemDetails",
        query: {
          questionId: item.id
        }
      });
    }
  },
  mounted() {
    // console.log(localStorage.getItem("questionKeyword"));
    if (localStorage.getItem("questionKeyword") != "") {
      this.keyword = localStorage.getItem("questionKeyword");
      this.searchBtn();
    }
  }
};



