import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      listArr: []
    };
  },
  methods: {
    // 进入页面回去列表
    getList() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/app/consult/")
        .then(res => {
          console.log(res);
          this.$vux.loading.hide();
          if (res.data.status == 200001) {
            this.listArr = res.data.msg;
          } else {
            console.log("获取列表失败");
          }
        })
        .catch(err => {
          console.log(err);
          this.$vux.loading.hide();
        });
    }
  },
  mounted() {
    this.getList(); //
  }
};



