],
active: 1, //默认连续三天
repeat: "three_day", //默认转换提醒的每隔时间

eventId: "1" 
};
},
methods: {

addBtn() {
this.addShow = true;
},


getTips() {
base
.get("/api/app/drug_notice/?drug_id=" + this.id)
.then(res => {
console.log(res, "用药提醒列表");
if (res.data.status == 200121) {
this.tipsArr = res.data.msg;
for (var i = 0; i < this.tipsArr.length; i++) {
if (this.tipsArr[i].hour < 10) {
this.tipsArr[i].hour = "0" + this.tipsArr[i].hour;
}
if (this.tipsArr[i].minute < 10) {
this.tipsArr[i].minute = "0" + this.tipsArr[i].minute;
}
}
} else {
console.log("获取提醒列表失败");
}
})
.catch(err => {
console.log(err);
});
},


deleteTips(index, item) {
this.$vux.loading.show({
text: "Loading"
});
base
.delete("/api/app/drug_notice/" + item.id + "/")
.then(res => {
console.log(res);
this.$vux.loading.hide();
if (res.data.status == 200122) {
this.$vux.toast.show({
text: res.data.desc
});
this.getTips();
} else {
this.$vux.toast.show({
type: "text",
text: res.data.desc
});
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},


editTips() {


},


selectTime() {

},


saveBtn() {
this.addShow = false;
var data = {
drug_id: this.id,
hour: this.dataArr[0],
minute: this.dataArr[1],
repeat: this.repeat
};
this.$vux.loading.show({
text: "Loading"
});
base
.post("/api/app/drug_notice/", JSON.stringify(data))
.then(res => {
console.log(res);
this.$vux.loading.hide();
if (res.data.status == 200120) {
this.$vux.toast.show({
text: res.data.desc
});
this.getTips();
} else {
this.$vux.toast.show({
type: "text",
text: res.data.desc
});
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},
cancelBtn() {
this.addShow = false;
},

selectBtn(index, item) {
this.active = item.id;
if (item.id == 0) {
this.repeat == "never";
} else if (item.id == 1) {
this.repeat == "three_day";
} else if (item.id == 2) {
this.repeat == "five_day";
} else if (item.id == 3) {
this.repeat == "seven_day";
} else if (item.id == 4) {
this.repeat == "every_day";
}
}
},
mounted() {
if (this.$route.query) {
this.id = this.$route.query.id;
this.name = this.$route.query.name;
this.getTips(); 
}
},
components: {
Picker
}
};



