import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      hotList: [],

      listArr: [],

      historyArr: [],

      keyword: "",

      noSearchShow: true //默认显示热门历史
    };
  },
  methods: {
    watchInput() {
      if (this.keyword == "") {
        this.noSearchShow = true;
      }
    },

    searchBtn() {
      if (this.keyword == "") {
        return;
      } else {
        this.historyArr = this.historyArr.filter(v => v != this.keyword);
        this.historyArr.unshift(this.keyword);
        localStorage.setItem("problemHistory", JSON.stringify(this.historyArr));
        localStorage.setItem("questionKeyword", this.keyword);
        this.$router.push({
          path: "searchProblemList"
        });
      }
    },

    hotListBtn(index, item) {
      this.keyword = item.keyword;
      this.searchBtn();
    },

    // 点击删除所有历史记录
    delAllHistory() {
      localStorage.removeItem("problemHistory");
      this.historyArr = [];
    },

    // 点击删除单个历史记录
    delHistoryItem(index, item) {
      this.historyArr.splice(index, 1);
      var tempArr = this.historyArr;
      localStorage.setItem("problemHistory", JSON.stringify(tempArr));
    },

    // 点击进入问题详情
    questionDetails(index, item) {},

    // 点击清楚输入框内容
    delKeyword() {
      this.keyword = "";
    },

    //进入页面获取热门问题列表
    getHotList() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/app/hot_search_key/question/")
        .then(res => {
          this.$vux.loading.hide();
          console.log(res);
          if (res.data.status == 200001) {
            this.hotList = res.data.msg;
          } else {
            console.log("获取失败");
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          consoel.log(err);
        });
    }
  },
  mounted() {
    this.getHotList(); //进入页面获取热门问题列表
    console.log();
    if (JSON.parse(localStorage.getItem("problemHistory")) == null) {
      this.historyArr = [];
    } else {
      this.historyArr = JSON.parse(localStorage.getItem("problemHistory"));
    }
  }
};



