
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import base from "../../components/request/baseUrl.js";
export default {
name: "home",
data() {
return {
msg: "Welcome to Your Vue.js App",
search: "", //搜索内容
whole: [
{
img: require("../../assets/medicine-class-icon4.png"),
name: "西药子类"
},
{
img: require("../../assets/medicine-class-icon1.png"),
name: "中成药子类"
},
{
img: require("../../assets/medicine-class-icon2.png"),
name: "酸根盐基"
},
{ img: require("../../assets/medicine-class-icon3.png"), name: "剂型" }
],
Commonly: [],
Recom: [], //要是推荐列表
hospitalArr: []
};
},
methods: {

sortBtn(index, item) {
if (item.name == "酸根盐基") {
this.$router.push({
path: "findList"
});
} else if (item.name == "剂型") {
this.$router.push({
path: "findAcidDrug"
});
} else {
this.$router.push({
path: "findItemDrug",
query: {
name: item.name
}
});
}
},


commonUseBtn() {
this.$router.push({
path: "commonUse",
query: {
name: "常用药品"
}
});
},


commonUseDetails(index, item) {
this.$router.push({
path: "findListDetails",
query: {
id: item.id
}
});
},


getCommonUse() {
this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/app/common_drug/")
.then(res => {
this.$vux.loading.hide();
console.log(res);
if (res.data.status == 200001) {
this.Commonly = res.data.msg;
} else {
console.log("获取药品列表失败");
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},


searchBtn() {
this.$router.push({
path: "findSearch"
});
},


listBtn() {
this.$router.push({
path: "findList"
});
},


getSuggest() {
base
.get("/api/app/recommend_drug/")
.then(res => {
console.log(res, "获取药师推荐列表");
if (res.data.status == 200001) {
this.Recom = res.data.msg;
} else {
console.log("药师推荐列表获取失败");
}
})
.catch(err => {
console.log("药师推荐列表获取失败");
});
},


suggestDetails(index, item) {
this.$router.push({
path: "findListDetails",
query: {
id: item.id
}
});
},


hosListBtn() {
this.$router.push({
path: "hotHospital"
});
},


hosDetails(index, item) {
this.$router.push({
path: "findHosDetails",
query: {
id: item.id
}
});
},


getHospital() {
this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/app/hospital/")
.then(res => {
console.log(res);
this.$vux.loading.hide();
if (res.data.status == 200001) {
this.hospitalArr = res.data.msg;
} else {
console.log("获取医院失败");
}
})
.catch(err => {
console.log(err);
this.$vux.loading.hide();
});
}


},
mounted() {


this.getHospital(); 

this.getCommonUse(); 

this.getSuggest(); 
},
components: {
Tabbar,
TabbarItem,
Flexbox,
FlexboxItem
}
};



