import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      leftListArr: [], //
      rightListArr: [], //
      isActive: 0, //
      rightActive: -1, //
      num: "01",
      popShow: true, //
      listArr: [], //
      name: "子类名称",
      sortName: "" //弹窗顶部类别
    };
  },
  methods: {
    //输入框获取焦点跳转搜索yemian
    searchInput() {
      this.$router.push({
        path: "findSearch"
      });
    },

    //跳转到详情页
    details(index, item) {
      this.$router.push({
        path: "findListDetails",
        query: {
          id: item.id
        }
      });
    },

    selectBtn() {
      this.popShow = true;
      // this.getDetailsList();//请求第一条
    },

    getClassifyList() {
      this.$vux.loading.show({
        text: "Loading"
      });
      if (this.$route.query.name == "中成药子类") {
        base
          .get("/api/drug/subcategory/3/")
          .then(res => {
            this.$vux.loading.hide();
            console.log(res);
            if (res.data.status == 200001) {
              this.leftListArr = res.data.msg;
            } else {
              console.log("获取列表失败");
            }
          })
          .catch(err => {
            this.$vux.loading.hide();
            console.log(err);
          });
      } else {
        base
          .get("/api/drug/subcategory/1/")
          .then(res => {
            this.$vux.loading.hide();
            console.log(res);
            if (res.data.status == 200001) {
              this.leftListArr = res.data.msg;
            } else {
              console.log("获取列表失败");
            }
          })
          .catch(err => {
            this.$vux.loading.hide();
            console.log(err);
          });
      }
    },

    //   选择左侧分类
    selectClassify(index, item) {
      this.isActive = index;
      this.num = item.sub_code_1;
      this.rightActive = -1;
      this.getDetailsList(); //重新赋值num进行请求
    },

    // 渲染右侧列表
    getDetailsList() {
      this.$vux.loading.show({
        text: "Loading"
      });
      if (this.$route.query.name == "中成药子类") {
        base
          .get("/api/drug/subcategory/3/" + this.num + "/")
          .then(res => {
            console.log(res);
            this.$vux.loading.hide();
            if (res.data.status == 200001) {
              this.rightListArr = res.data.msg;
              // this.name = res.data.msg[0].sub_name_2;
            } else {
              console.log("获取列表失败");
            }
          })
          .catch(err => {
            console.log(err);
            this.$vux.loading.hide();
          });
      } else {
        base
          .get("/api/drug/subcategory/1/" + this.num + "/")
          .then(res => {
            console.log(res);
            this.$vux.loading.hide();
            if (res.data.status == 200001) {
              this.rightListArr = res.data.msg;
              // this.name = res.data.msg[0].sub_name_2;
            } else {
              console.log("获取列表失败");
            }
          })
          .catch(err => {
            console.log(err);
            this.$vux.loading.hide();
          });
      }
    },

    // 选择右侧分类渲染
    selectItem(index, item) {
      this.rightActive = index;
      console.log(item);
      this.name = item.sub_name_2;
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/drug/drug/?search_type=sub_code/" + item.sub_code)
        .then(res => {
          console.log(res);
          this.$vux.loading.hide();
          this.popShow = false;
          if (res.data.status == 200001) {
            this.listArr = res.data.msg;
          } else {
            console.log("获取列表失败");
          }
        })
        .catch(err => {
          console.log(err);
          this.$vux.loading.hide();
        });
    }
  },
  mounted() {
    if (this.$route.query) {
      this.sortName = this.$route.query.name;
      this.getClassifyList();
      this.getDetailsList();
    }
  }
};



