import base from "../../components/request/baseUrl.js";
// import { setTimeout } from 'timers';
export default {
  data() {
    return {
      checked: true,
      phone: "", //手机号
      code: "", //验证码
      getCodeBtnDisabled: false, //
      time: 60, //
      sendMsgDisabled: false,
      codeImg: "", //图形验证码
      codeImgUrl: "" //图像验证码链接
    };
  },
  methods: {
    // 进入页面获取图形验证码
    getImgCode() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .post("/api/auth/graph_verify_code/")
        .then(res => {
          console.log(res);
          this.$vux.loading.hide();
          if (res.data.status == 201008) {
            this.codeImgUrl = res.data.msg.code_url;
          } else {
            console.log("获取图形验证码失败");
          }
        })
        .catch(err => {
          console.log(err);
          this.$vux.loading.hide();
        });
    },

    // 更换一张验证码
    changeCodeImg() {
      this.getImgCode();
    },

    // 点击获取验证码
    getCode() {
      var phoneReg = /^[1][2,3,4,5,6,7,8,9][0-9]{9}$/;
      if (this.phone == "" || !phoneReg.test(this.phone)) {
        this.$vux.toast.show({
          type: "text",
          text: "请输入正确的手机号"
        });
      } else if (this.codeImg == "" || this.codeImg.length != 4) {
        this.$vux.toast.show({
          type: "text",
          text: "请输入正确图形验证码"
        });
      } else {
        this.$vux.loading.show({
          text: "Loading"
        });
        base
          .post("/api/auth/sms_code/bind_phone/", {
            mobile: this.phone,
            graph_verify_code: this.codeImg
          })
          .then(res => {
            console.log(res);
            this.$vux.loading.hide();
            if (res.data.status == 201006) {
              this.$vux.toast.show({
                text: "获取验证码成功"
              });
              this.getCodeBtnDisabled = true;
              let me = this;
              me.sendMsgDisabled = true;
              let interval = window.setInterval(function() {
                if (me.time-- <= 0) {
                  me.time = 60;
                  me.sendMsgDisabled = false;
                  window.clearInterval(interval);
                  me.getCodeBtnDisabled = false;
                }
              }, 1000);
            } else {
              this.$vux.toast.show({
                type: "text",
                text: res.data.desc
              });
            }
          })
          .catch(err => {
            console.log(err);
            this.$vux.loading.hide();
          });
      }
    },

    // 点击绑定
    bindBtn() {
      var phoneReg = /^[1][2,3,4,5,6,7,8,9][0-9]{9}$/;
      if (this.phone == "" || !phoneReg.test(this.phone)) {
        this.$vux.toast.show({
          type: "text",
          text: "请输入正确的手机号"
        });
      } else if (this.code == "" || this.code.length != 6) {
        this.$vux.toast.show({
          type: "text",
          text: "请输入正确的验证码"
        });
      } else if (this.checked == false) {
        this.$vux.toast.show({
          type: "text",
          text: "请先阅读并同意隐私条款"
        });
      } else {
        base
          .post("/api/auth/user/bind_phone/", {
            sms_code: this.code,
            mobile: this.phone
          })
          .then(res => {
            console.log(res);
            if (res.data.status == 201007) {
              this.$vux.toast.show({
                type: "text",
                text: "绑定成功"
              });
              setTimeout(() => {
                this.$router.push({
                  path: "my"
                });
              }, 1000);
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },

    // 点击右侧清楚手机号按钮
    delPhone() {
      this.phone = "";
    }
  },
  mounted() {
    this.getImgCode(); //进入页面获取图形验证码
  }
};



