
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import base from "../../components/request/baseUrl.js";
export default {
name: "home",
data() {
return {
bannersArr: [], //轮播图列表
classifyArr: [], //病类分类列表
knowledgeArr: [], //药物常识
newsArr: [
{id:'1',name:'国内动态',img:require('../../assets/home-news-photo1.png')},
{id:'2',name:'国际前沿',img:require('../../assets/home-news-photo2.png')},
{id:'3',name:'医院新闻',img:require('../../assets/home-news-photo3.png')}],//行业新闻
};
},

methods: {

knowledgeBtn() {
this.$router.push({
path: "homeKnowledge"
});
},

knowledgeDetails(index,item){
this.$router.push({
path: 'homeNewsDetails',
query: {
id: item.id,
name: '药物常识'
}
})
},





healthBtn() {
this.$router.push({
path: "homeHealth"
});
},


classifyBtn() {
this.$router.push({
path: "homeClassify"
});
},

classifyItemBtn(index, item) {
this.$router.push({
path: "homeClassify",
query: {
id: item.id
}
});
},


searchBtn() {
this.$router.push({
path: "findSearch"
});
},




getBanners() {
this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/app/advertise/")
.then(res => {
this.$vux.loading.hide();
if (res.data.status == 200101) {
this.bannersArr = res.data.msg;
} else {
console.log("获取轮播图失败");
}
})
.catch(err => {
console.log(err);
this.$vux.loading.hide();
});
},


getClassify() {
this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/drug/department/?index_num=5")
.then(res => {
console.log(res);
this.$vux.loading.hide();
if (res.data.status == 200201) {
this.classifyArr = res.data.msg;
} else {
console.log("获取病类分类失败");
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},


getKnowledge() {

this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/app/drug_sense/?page=1&page_size=2")
.then(res => {
console.log(res,"药物常识列表")
this.$vux.loading.hide();
if (res.data.status == 200001) {
this.knowledgeArr = res.data.msg;
} else {
console.log("获取药物常识列表失败");
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},


newsListBtn(index,item){
this.$router.push({
path: 'homeNews',
query: {
id: item.id
}
})
}



},
created() {},
mounted() {

this.getBanners();

this.getClassify(); 

this.getKnowledge(); 
},
components: {
Tabbar,
TabbarItem,
Flexbox,
FlexboxItem,
Swiper,
SwiperItem
}
};



