
import base from "../../components/request/baseUrl.js";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
data() {
return {
roomArr: [], //科室列表
topListArr: [
{ name: "科室" },
{ name: "综合排序" },
{ name: "医生职称" }
], //顶部三个分类列表
sortArr: [
{ name: "综合排序" },
{ name: "粉丝数量" },
{ name: "综合评分" }
], //排序数组
titleArr: [
{ name: "医师" },
{ name: "主治医师" },
{ name: "副主任医师" },
{ name: "主任医师" }
], //医生职称Arr
doctorListArr: [], //医生列表

roomShow: false, //遮罩显示隐藏
roomListShow: false, //科室列表显示隐藏
sortListShow: false, //综合排序列表
titleListShow: false, //医生职称排序
titleIconShow: true, //右侧小图标显示隐藏
roomIconShow: true,       sortIconShow: true,       isActive: -1, //选中顶部的第几项
listActive: 0, //科室下方选中样式
titleActive: 3,       roomName: "全部科室", //科室名称
sortName: "综合排序",
titleName: "主任医师",
selectRoomName: "", //选择的科室名称
selectDoctorName: "主任医师", //选择的职称名称
selectSortName: "score"
};
},
methods: {

getRoomList() {
base
.get("/api/drug/department/")
.then(res => {
console.log(res);
if (res.data.status == 200201) {
this.roomArr = res.data.msg;
this.roomArr.push({ name: "全部科室" });
if (JSON.stringify(this.$route.query) == "{}") {
this.roomName = "全部科室";
this.selectRoomName = "";
this.isActive = this.roomArr.length - 1;
this.getDoctorList();
} else {
this.selectRoomName = this.$route.query.room;
this.isActive = this.$route.query.roomId - 1;
this.roomName = this.$route.query.room;
this.getDoctorList();
}
} else {
console.log("获取科室列表失败");
}
})
.catch(err => {
console.log(err);
});
},

roomBtn() {
this.roomShow = true;
this.roomListShow = true;
this.sortListShow = false;
this.titleListShow = false;
this.roomIconShow = false;
this.sortIconShow = true;
this.titleIconShow = true;
},
sortBtn() {
this.roomShow = true;
this.sortListShow = true;
this.roomListShow = false;
this.titleListShow = false;
this.sortIconShow = false;
this.titleIconShow = true;
this.roomIconShow = true;
},
titleBtn() {
this.roomShow = true;
this.titleListShow = true;
this.sortListShow = false;
this.roomListShow = false;
this.titleIconShow = false;
this.roomIconShow = true;
this.sortIconShow = true;
},


getDoctorList() {
this.$vux.loading.show({
text: "Loading"
});
base
.get(
"/api/app/doctor/?department=" +
this.selectRoomName +
"&professional_title=" +
this.selectDoctorName +
"&sort_by=" +
this.selectSortName
)
.then(res => {
console.log(res, "获取医生列表");
this.$vux.loading.hide();
if (res.data.status == 200001) {
this.doctorListArr = res.data.msg;
this.roomShow = false;
} else {
console.log("获取列表失败");
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},


doctorConsultBtn(index, item) {
localStorage.setItem("doctorId", item.id);
this.$router.push({
path: "doctorDetails"
});
},


selectRoom(index, item) {
this.roomShow = true; 

this.roomName = item.name;
this.isActive = index;
if (item.name == "全部科室") {
this.selectRoomName = "";
this.getDoctorList();
} else {
this.selectRoomName = item.name;
this.getDoctorList();
}
},






selectSort(index, item) {
this.roomShow = true; 
this.listActive = index;
this.sortName = item.name;
if (item.name == "粉丝数量") {
this.selectSortName == "fans";
this.getDoctorList();
} else {
this.selectSortName = "score";
this.getDoctorList();
}
},
selectTitle(index, item) {
this.titleName = item.name;
this.roomShow = true; 
this.titleActive = index;
this.selectDoctorName = item.name;
this.getDoctorList();
},


preDefault() {
this.roomShow = false;
}
},
mounted() {
this.getRoomList(); 









},
components: {
Flexbox,
FlexboxItem
}
};



