
import base from "../../components/request/baseUrl.js";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
data() {
return {
flexArr: [
{ id: 1, name: "药师说药" },
{ id: 2, name: "用药知识" },
{ id: 3, name: "药物辨认" },
{ id: 4, name: "药物储存" },
{ id: 5, name: "不良反应" },
{ id: 6, name: "安全用药" },
{ id: 7, name: "其他" }
],
listArr: [],       isActive: 1
};
},
methods: {
swiper(id) {
this.isActive = id;
this.getList();
},

getList() {
this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/app/drug_sense/?sense_type=" + this.isActive)
.then(res => {
this.$vux.loading.hide();
console.log(res, "药物常识列表");
if (res.data.status == 200001) {
this.listArr = res.data.msg;
} else {
console.log("获取常识列表失败");
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},


detailsBtn(index, item) {
localStorage.setItem("homeKnowledgeActive",this.isActive);
this.$router.push({
path: "homeNewsDetails",
query: {
id: item.id,
name: "药物常识"
}
});
}
},
beforeRouteEnter (to, from, next) {

next((vm)=>{
if(from.path == '/homeNewsDetails'){
vm.isActive = localStorage.getItem("homeKnowledgeActive");
vm.getList();
}else {
vm.getList();
}
})
},
mounted() {

},
components: {
Tab,
TabItem
}
};



