
import base from "../../components/request/baseUrl.js";
import Scroller from 'vux/src/components/scroller/index.vue'

import { setTimeout } from 'timers';
export default {
data() {
return {
listArr: [], //
keyword: "胃药",
clientY: "",
page: 1,
size: 10,
top: 500,
next: ""
};
},
methods: {
handleScroll() {
if (this.$refs.viewBox != undefined) {
let posi = this.$refs.viewBox.scrollTop || this.$refs.viewBox.scrollTop;
if (posi > this.top) {
this.top += 500;
this.page += 1;
if (this.next == "" || this.next == null) {
} else {
this.$vux.loading.show({
text: "Loading"
});
setTimeout(()=>{
this.$vux.loading.hide();
this.getList();
},1500)
}
}
}
},


details(index, item) {
this.$router.push({
path: "findListDetails",
query: {
id: item.id
}
});
},

searchBtn() {
if (this.keyword == "") {
return;
} else {
this.getList();

























}
},


watchInput() {
if (this.keyword == "") {
this.$router.push({
path: "findSearch"
});
}
},


getList() {
this.$vux.loading.show({
text: "Loading"
});
base
.get(
"/api/drug/drug/?keyword=" +
this.keyword +
"&page=" +
this.page +
"&page_size=" +
this.size
)
.then(res => {
console.log(res);
this.$vux.loading.hide();
if (res.data.status == 200001) {
this.next = res.data.next;

for (var i = 0; i < res.data.msg.length; i++) {
this.listArr.push(res.data.msg[i]);

}
} else if (res.data.status == 400000) {
console.log(res.data.desc);
} else {
console.log("获取失败");
}
})
.catch(err => {
console.log(err);
this.$vux.loading.hide();
});
}
},
destroyed() {
var _this = this;
window.removeEventListener("scroll", _this.handleScroll);
},
mounted() {
var _this = this;
window.addEventListener("scroll", _this.handleScroll, true);
if (this.$route.query) {
this.keyword = this.$route.query.keyword;
this.getList();
}
},
components: {
Scroller
}
};



