import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      problemList: [] //最新问题列表
    };
  },
  methods: {
    //   点击进入到详情页
    detailsBtn(index,item) {
      this.$router.push({
        path: "newProblemDetails",
        query: {
          questionId: item.id
        }
      });
    },

    // 获取最新问题列表
    getnewProblem() {
      base
        .get("/api/app/consult/")
        .then(res => {
          console.log(res,"互殴最新问题");
          if(res.data.status == 200001){
              this.problemList = res.data.msg;
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },

  mounted() {
    this.getnewProblem(); //
  }
};



