
import base from "../../components/request/baseUrl.js";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
data() {
return {
doctorId: "", //医生ID
doctorDetailsObj: {}, //医生信息
QAList: [] 
};
},
methods: {
doctorMsg() {
this.$router.push({
path: "doctorMsg",
query: {
id: this.doctorId
}
});
},


getDoctorMsg() {
this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/app/doctor/" + this.doctorId + "/")
.then(res => {
console.log(res, "获取医生信息");
this.$vux.loading.hide();
if (res.data.status == 200104) {
this.doctorDetailsObj = res.data.msg;
} else {
console.log(res.data.desc);
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},


getQAList() {
this.$vux.loading.show({
text: "Loading"
});
base
.get(
"/api/app/doctor_answer/?doctor_id=" +
this.doctorId +
"&page=1&page_size=3"
)
.then(res => {
this.$vux.loading.hide();
console.log(res, "获取问答列表");
if (res.data.status == 200001) {
this.QAList = res.data.msg;
} else {
console.log("获取问答列表失败");
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},


attentionBtn() {
this.$vux.loading.show({
text: "Loading"
});
base
.post("/api/app/favor_doctor/", {
doctor_id: this.doctorId
})
.then(res => {
console.log(res);
this.$vux.loading.hide();
if (res.data.status == 200110) {
this.$vux.toast.show({
text: res.data.desc
});
this.getDoctorMsg();
} else {
this.$vux.toast.show({
type: "text",
text: res.data.desc
});
}
})
.catch(err => {
console.log(err);
this.$vux.loading.hide();
});
},


cancelAttentionBtn() {
this.$vux.loading.show({
text: "Loading"
});
base
.delete("/api/app/favor_doctor/" + this.doctorId + "/")
.then(res => {
this.$vux.loading.hide();
console.log(res);
if (res.data.status == 200111) {
this.$vux.toast.show({
text: res.data.desc
});
this.getDoctorMsg();
} else {
this.$vux.toast.show({
type: 'text',
text: res.data.desc
});
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},


moreDetailsBtn() {
this.$router.push({
path: "homeDoctorAnswer",
query: {
id: this.doctorId
}
});
},


answerDetails() {
this.$router.push({
path: "newProblemDetails"
});
},


consultBtn() {
localStorage.setItem("doctorId",this.doctorId)
this.$router.push({
path: "homeConsult",
});
}
},
mounted() {
this.doctorId = localStorage.getItem("doctorId");
this.getDoctorMsg(); 

this.getQAList(); 
},
components: {
Flexbox,
FlexboxItem
}
};



