import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      leftListArr: [], //左侧列表数据
      rightListArr: [], //右侧
      listArr: [], //选择后渲染的图片和数据
      isActive: 0, //默认高亮左侧的第一个
      rightActive: 0, //右侧高亮第一个
      num: "00", //默认请求第一条数据渲染右侧
      popShow: true, //
      name: "" //
    };
  },
  methods: {
    searchFocus() {
      this.$router.push({
        path: "findSearch"
      });
    },

    selectBtn() {
      this.popShow = true;
      this.getDetailsList(); //请求第一条
    },

    // 获取左侧列表
    getClassifyList() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/drug/solution_category/")
        .then(res => {
          this.$vux.loading.hide();
          console.log(res);
          if (res.data.status == 200001) {
            this.leftListArr = res.data.msg;
          } else {
            console.log("获取列表失败");
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
        });
    },

    // 进入页面默认请求左侧列表的第一条数据
    getDetailsList() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/drug/solution_category/" + this.num + "/")
        .then(res => {
          console.log(res);
          this.$vux.loading.hide();
          if (res.data.status == 200001) {
            this.rightListArr = res.data.msg;
            this.name = res.data.msg[0].sub_name_2;
          } else {
            console.log("获取列表失败");
          }
        })
        .catch(err => {
          console.log(err);
          this.$vux.loading.hide();
        });
    },

    // 点击左侧选择分类
    selectClassify(index, item) {
      this.isActive = index;
      this.num = item.sub_code_1;
      this.getDetailsList(); //重新赋值num进行请求
    },

    //点击右侧元素子类
    selectItem(index, item) {
      this.rightActive = index;
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/drug/drug/?search_type=solution_code/" + item.sub_code)
        .then(res => {
          console.log(res);
          this.popShow = false;
          this.$vux.loading.hide();
          if (res.data.status == 200001) {
            this.listArr = res.data.msg;
          } else {
            console.log("获取列表失败");
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
        });
    },

    // 点击渲染出来的数据进入药品详情
    listDetails(index, item) {
      this.$router.push({
        path: "findListDetails",
        query: {
          id: item.id
        }
      });
    }
  },
  mounted() {
    this.getClassifyList(); //获取剂型分类
    this.getDetailsList();
  }
};



