import base from "../../components/request/baseUrl.js";
import { setTimeout } from 'timers';
export default {
  data() {
    return {
      questionId: "",
      imgsArr: [],
      answerArr: [],
      detailsObj: {},//
    };
  },
  methods: {
      backHome(){
          this.$vux.loading.show({
            text: 'Loading'
            });
          setTimeout(()=>{
            this.$vux.loading.hide();
            this.$router.push({path: 'home'})
          },1000)
      },


        // 获取问题详情
      getQuestionDetails(){
          base.get('/api/app/consult/'+ this.questionId +'/')
          .then(res=>{
              console.log(res);
              if(res.data.status == 200107){
                  this.detailsObj = res.data.msg;
                  this.imgsArr = res.data.msg.image_consult;
                  this.answerArr = res.data.msg.consult_answers;
              }
          }).catch(err=>{
              console.log(err);
          })
      }
  },
  beforeMount() {
    this.$vux.toast.hide();
  },
  mounted() {
    this.questionId = this.$route.query.id;

    this.getQuestionDetails();//获取问题详情
  }
};



