
import base from "../../components/request/baseUrl.js";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
data() {
return {
roomArr: [], //科室列表
doctorArr: [] 
};
},
methods: {
searchBtn() {
this.$router.push({
path: "searchProblem"
});
},
consultBtn(index, item) {
localStorage.setItem("doctorId", item.id);
this.$router.push({
path: "doctorDetails"
});
},


getDoctor() {
base
.get("/api/app/doctor/?best_num=3")
.then(res => {
console.log(res);
if (res.data.status == 200001) {
this.doctorArr = res.data.msg;
} else {
console.log("获取医生列表失败");
}
})
.catch(err => {
console.log(err);
});
},


getRoom() {
this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/drug/department/")
.then(res => {
this.$vux.loading.hide();
console.log(res);
if (res.data.status == 200201) {
this.roomArr = res.data.msg;
} else {
console.log("获取列表失败");
}
})
.catch(err => {
console.log(err);
this.$vux.loading.hide();
});
},


selectRoomBtn(index, item) {
console.log(item);
this.$router.push({
path: "homeSelectRoom",
query: {
room: item.name,
roomId: item.id
}
});
}
},
mounted() {

this.getDoctor(); 

this.getRoom(); 
},
components: {
Flexbox,
FlexboxItem
}
};



