import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      roomArr: [], //科室列表
      listArr: [],//列表的渲染数组
      roomId: 0,//科室ID
      pageNum: 1,//分页默认一页
      pageSizeNum: 10,//
      isActive: 0,//默认选择的id
    };
  },
  methods: {
    // 跳转到药品的详情页
    detailsBtn() {
      // this.$router.push({
      //   path: "findListDetails"
      // });
    },

    // 点击选择左侧科室
    selectRoom(id){
      this.isActive=id;
      this.roomId = id;
      this.getList();
    },



    // 获取病兆分类科室选项
    getRoom() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/drug/department/")
        .then(res => {
          this.$vux.loading.hide();
          console.log(res);
          if (res.data.status == 200201) {
            this.roomArr = res.data.msg;
          } else {
            console.log("获取列表失败");
          }
        })
        .catch(err => {
          console.log(err);
          this.$vux.loading.hide();
        });
    },

    // 获取分类列表渲染
    getList(){
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/drug/disease/" + this.roomId + '/?page=' + this.pageNum + '&page_size=' + this.pageSizeNum)
        .then(res => {
          this.$vux.loading.hide();
          console.log(res);
          if(res.data.status == 200001){
            this.listArr = res.data.msg;
          }else {
            console.log("获取列表失败")
          }
        })
        .catch(err => {
          console.log(err);
          this.$vux.loading.hide();
        });
    },


    // 获取跳转页面传的科室ID的值
    getItemId(){
      
    }
  },
  mounted() {
    if(this.$route.query.id != undefined){
      this.isActive = this.$route.query.id;
      this.roomId = this.$route.query.id;
    }

    this.getRoom(); //获取科室列表

    this.getList();//获取科室列表下的数据
    console.log(this.$route.query)
  }
};



