import base from '../../components/request/baseUrl.js';
export default {
    data(){
        return{
            listArr: [],//左侧列表
            detailsListArr: [],//右侧列表数据
            isActive: 0,//默认选择的id
            code: '00',
        }
    },
    methods: {
        details(index,item){
            this.$router.push({
                path: 'findListDetails',
                query: {
                    id: item.id
                }
            })
        },


        // 点击选择右侧列表的渲染
        selectList(index,item){
            this.isActive=index;
            this.code = item.code;
            this.getMedicalList();
        },

        // 获取左侧列表
        getList(){
            this.$vux.loading.show({
                text: "Loading"
            });
            base.get('/api/drug/acid_base_category/')
            .then(res=>{
                this.$vux.loading.hide();
                console.log(res);
                if(res.data.status == 200001){
                    this.listArr = res.data.msg;
                }else {
                    console.log("获取列表失败");
                }
            }).catch(err=>{
                this.$vux.loading.hide();
                console.log(err);
            })
        },

        getMedicalList(){
            this.$vux.loading.show({
                text: "Loading"
            });
            base
            .get("/api/drug/drug/?search_type=acid_base_code&search_code=" + this.code)
            .then(res => {
                console.log(res);
                this.$vux.loading.hide();
                if (res.data.status == 200001) {
                    this.detailsListArr = res.data.msg;
                } else {
                console.log("获取列表失败");
                }
            })
            .catch(err => {
                console.log(err);
                this.$vux.loading.hide();
            });
        }
    },
    mounted(){
        this.getList();//获取左侧列表
        this.getMedicalList();
    },
}



