import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/pages/home/home.vue';
import HomeHealth from '@/pages/home/homeHealth.vue';
import HomeConsult from '@/pages/home/homeConsult.vue';
import SearchProblem from '@/pages/home/searchProblem.vue';
import NewProblem from '@/pages/home/newProblem.vue';
import HomePatientModify from '@/pages/home/homePatientModify.vue';
import HomeSelectRoom from '@/pages/home/homeSelectRoom.vue';
import HomeNews from '@/pages/home/homeNews.vue';
import HomeNewsDetails from '@/pages/home/homeNewsDetails.vue';
import NewProblemDetails from '@/pages/home/newProblemDetails.vue';
import HomeClassify from '@/pages/home/homeClassify.vue';
import DoctorDetails from '@/pages/home/doctorDetails.vue';
import DoctorMsg from '@/pages/home/doctorMsg.vue';
import HomeKnowledge from '@/pages/home/homeKnowledge.vue';
import HomeSearchMedical from '@/pages/home/homeSearchMedical.vue';
import HomeConsultSuccess from '@/pages/home/homeConsultSuccess.vue';
import HomeDoctorAnswer from '@/pages/home/homeDoctorAnswer.vue';
import SearchProblemList from '@/pages/home/searchProblemList.vue';



import Find from '@/pages/find/find.vue';
import CommonUse from '@/pages/find/commonUse.vue';
import HotHospital from '@/pages/find/hotHospital.vue';
import FindSearch from '@/pages/find/search.vue';
import FindList from '@/pages/find/findList.vue';
import FindHosDetails from '@/pages/find/HosDetails.vue';
import FindListDetails from '@/pages/find/details.vue';
import FindAcidDrug from '@/pages/find/findAcidDrug.vue';
import FindItemDrug from '@/pages/find/findItemDrug.vue';
import FindSearchList from '@/pages/find/findSearchList.vue';


import My from '@/pages/my/my.vue';
import Binding from '@/pages/my/binding.vue';
import Realname from '@/pages/my/realname.vue';
import Details from '@/pages/my/details.vue';
import AskMedicine from '@/pages/my/askMedicine.vue';
import MyConcernedDoctors from '@/pages/my/myConcernedDoctors.vue';
import MedicineCollect from '@/pages/my/medicineCollect.vue';
import StudyRecord from '@/pages/my/studyRecord.vue';
import About from '@/pages/my/about.vue';
import MyMedicalTips from '@/pages/my/myMedicalTips.vue';
Vue.use(Router);

export default new Router({
    routes: [{
            path: '/',
            redirect: to => {
                //在此处可以判断是否登录
                return { path: '/home' }
            }
        }, {
            path: '/home',
            name: 'home',
            component: Home,
            meta: {
                title: '寻药网',
                allowBack: false
            }
        },
        {
            path: '/homeHealth',
            name: 'homeHealth',
            component: HomeHealth,
            meta: {
                title: '健康问答',
            }
        },
        {
            path: '/homeConsult',
            name: 'homeConsult',
            component: HomeConsult,
            meta: {
                title: '发布咨询',
            }
        },
        {
            path: '/homeConsultSuccess',
            name: 'homeConsultSuccess',
            component: HomeConsultSuccess,
            meta: {
                title: '发布咨询'
            }
        },
        {
            path: '/searchProblem',
            name: 'searchProblem',
            component: SearchProblem,
            meta: {
                title: '问题搜索'
            }
        },
        {
            path: '/newProblem',
            name: 'newProblem',
            component: NewProblem,
            meta: {
                title: '最新问题'
            }
        },
        {
            path: '/newProblemDetails',
            name: 'newProblemDetails',
            component: NewProblemDetails,
            meta: {
                title: '最新问题'
            }
        },
        {
            path: '/homePatientModify',
            name: 'homePatientModify',
            component: HomePatientModify,
            meta: {
                title: '资料更改'
            }
        },
        {
            path: '/homeSelectRoom',
            name: 'homeSelectRoom',
            component: HomeSelectRoom,
            meta: {
                title: '科室选择'
            }
        },
        {
            path: '/homeNews',
            name: 'homeNews',
            component: HomeNews,
            meta: {
                title: '行业新闻'
            }
        },
        {
            path: '/homeNewsDetails',
            name: 'homeNewsDetails',
            component: HomeNewsDetails,
            meta: {
                title: '行业新闻'
            }
        },
        {
            path: '/homeClassify',
            name: 'homeClassify',
            component: HomeClassify,
            meta: {
                title: '病兆分类'
            }
        },
        {
            path: '/doctorDetails',
            name: 'doctorDetails',
            component: DoctorDetails,
            meta: {
                title: ''
            }
        },
        {
            path: '/doctorMsg',
            name: 'doctorMsg',
            component: DoctorMsg,
            meta: {
                title: ''
            }
        },
        {
            path: '/homeKnowledge',
            name: 'homeKnowledge',
            component: HomeKnowledge,
            meta: {
                title: '药物常识'
            }
        },
        {
            path: '/homeSearchMedical',
            name: 'homeSearchMedical',
            component: HomeSearchMedical,
            meta: {
                title: '中成药子类'
            }
        },
        {
            path: '/homeDoctorAnswer',
            name: 'homeDoctorAnswer',
            component: HomeDoctorAnswer,
            meta: {
                title: '患者问答'
            }
        },
        {
            path: '/searchProblemList',
            name: 'searchProblemList',
            component: SearchProblemList,
            meta: {
                title: '问题搜索'
            }
        },





        // 寻药
        {
            path: '/find',
            name: 'find',
            component: Find,
            meta: {
                title: '寻药',
                allowBack: false
            }
        },
        {
            path: '/commonUse',
            name: 'commonUse',
            component: CommonUse,
            meta: {
                title: '常用药品'
            }
        },
        {
            path: '/hotHospital',
            name: 'hotHospital',
            component: HotHospital,
            meta: {
                title: '热门医院'
            }
        },
        {
            path: '/findSearch',
            name: 'findSearch',
            component: FindSearch,
            meta: {
                title: '药品搜索'
            }
        },
        {
            path: '/findList',
            name: 'findList',
            component: FindList,
            meta: {
                title: '酸根盐基'
            }
        },
        {
            path: '/findHosDetails',
            name: 'findHosDetails',
            component: FindHosDetails,
            meta: {
                title: '热门医院'
            }
        },
        {
            path: '/findListDetails',
            name: 'findListDetails',
            component: FindListDetails,
            meta: {
                title: '详情'
            }
        },
        {
            path: '/findAcidDrug',
            name: 'findAcidDrug',
            component: FindAcidDrug,
            meta: {
                title: '剂型分类'
            }
        },
        {
            path: '/findItemDrug',
            name: 'findItemDrug',
            component: FindItemDrug,
            meta: {
                title: '中成药子类'
            }
        },
        {
            path: '/findSearchList',
            name: 'findSearchList',
            component: FindSearchList,
            meta: {
                title: '药品搜索'
            }
        },










        // 个人中心
        {
            path: '/my',
            name: 'my',
            component: My,
            meta: {
                title: '个人中心',
                allowBack: false
            }
        },
        {
            path: '/binding',
            name: 'binding',
            component: Binding,
            meta: {
                title: '绑定'
            }
        },
        {
            path: '/realname',
            name: 'realname',
            component: Realname,
            meta: {
                title: '实名认证'
            }
        },
        {
            path: '/details',
            name: 'details',
            component: Details,
            meta: {
                title: '详细资料'
            }
        },
        {
            path: '/askMedicine',
            name: 'askMedicine',
            component: AskMedicine,
            meta: {
                title: '我的问药'
            }
        },
        {
            path: '/myConcernedDoctors',
            name: 'myConcernedDoctors',
            component: MyConcernedDoctors,
            meta: {
                title: '我关注的医生'
            }
        },
        {
            path: '/medicineCollect',
            name: 'medicineCollect',
            component: MedicineCollect,
            meta: {
                title: '寻药收藏'
            }
        },
        {
            path: '/studyRecord',
            name: 'studyRecord',
            component: StudyRecord,
            meta: {
                title: '学习记录'
            }
        },
        {
            path: '/about',
            name: 'about',
            component: About,
            meta: {
                title: '关于我们'
            }
        },
        {
            path: '/myMedicalTips',
            name: 'myMedicalTips',
            component: MyMedicalTips,
            meta: {
                title: '用药提醒'
            }
        },
    ]
})


