
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import { mapGetters, mapActions } from "vuex";
export default {
name: "App",
data() {
return {
select: "首页",
show: false,
};
},

methods: {
onIndexChange(newIndex, oldIndex) {

}
},
components: {
Tabbar,
TabbarItem
},
mounted(){
if(this.$route.path == '/' || this.$route.path == '/home' || this.$route.path == '/find' || this.$route.path == '/my'){
this.$store.dispatch('showTabar',true);
}else {
this.$store.dispatch('hideTabar',false);
};

},
watch:{
$route(to,from){
if(to.path == '/' || to.path == '/home' || to.path == '/find' || to.path == '/my'){
this.$store.dispatch('showTabar',true);
}else {
this.$store.dispatch('hideTabar',false);
}
}
}
};



