
import base from '../../components/request/baseUrl.js';
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
name: 'home',
data () {
return {
id: '',//列表ID
details: {},        length: '',        img: '',
collectStatus: '',
}
},
methods: {

getDetails(){
this.$vux.loading.show({
text: "Loading"
});
base.get('/api/drug/drug/'+ this.id + '/')
.then(res=>{
console.log(res,"药品详情");
this.$vux.loading.hide();
if(res.data.status == 200206){
this.details = res.data.msg;
this.length = res.data.msg.drug_images.length;
this.collectStatus = res.data.msg.is_favor;
if(res.data.msg.drug_images.length>0){
this.img = res.data.msg.drug_images[0].image_url
}else {
this.img = require('../../assets/drug-default.png')
}
}else {
console.log("获取详情失败")
}
}).catch(err=>{
console.log(err);
this.$vux.loading.hide();
})
},


collectBtn(){
base.post('/api/app/favor_drug/',JSON.stringify({
drug_id: this.id
})).then(res=>{
console.log(res)
if(res.data.status == 200112){
this.$vux.toast.show({
text: res.data.desc
});
this.getDetails();
}else {
this.$vux.toast.show({
type: 'text',
text: res.data.desc
})
}
}).catch(err=>{
console.log(err)
})
},


cancelCollectBtn(){
base.delete('/api/app/favor_drug/' + this.id + '/').then(res=>{
console.log(res)
if(res.data.status == 200113){
this.$vux.toast.show({
text: res.data.desc
});
this.getDetails();
}else {
this.$vux.toast.show({
type: 'text',
text: res.data.desc
})
}
}).catch(err=>{
console.log(err)
})
},



},
mounted(){
console.log(this.$route.query)
if(this.$route.query){
this.id = this.$route.query.id;
this.getDetails();
};


},
components: {
Tabbar,
TabbarItem
}
}



