import base from "../../components/request/baseUrl.js";
export default {
  data() {
    return {
      listArr: []
    };
  },
  methods: {
    // 获取列表
    getList() {
      this.$vux.loading.show({
        text: "Loading"
      });
      base
        .get("/api/app/favor_doctor/")
        .then(res => {
          console.log(res);
          this.$vux.loading.hide();
          if (res.data.status == 200001) {
            this.listArr = res.data.msg;
          } else {
            this.$vux.toast.show({
              type: "text",
              text: res.data.desc,
              isShowMask: true
            });
          }
        })
        .catch(err => {
          console.log(err);
          this.$vux.loading.hide();
        });
    },

    // 点击列表进入详情
    detailsBtn(index, item) {
      localStorage.setItem("doctorId", item.id);
      this.$router.push({
        path: "doctorDetails"
      });
    }
  },
  mounted() {
    this.getList();
  }
};



