
import base from "../../components/request/baseUrl.js";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
data() {
return {
age: "",
name: "",
phone: "",
doctorId: "",
sexArr: ["男", "女"],
selected: 0
};
},
methods: {
sexBtn_m(index, item) {
this.selected = index;
},

saveBtn() {
var phoneReg = /^1(3|4|5|6|7|8|9)\d{9}$/;
if (this.age < 0 || this.age>120) {
this.$vux.toast.show({
type: "text",
text: "请输入正确的年龄"
});
} else if (!phoneReg.test(this.phone)) {
this.$vux.toast.show({
type: "text",
text: "请输入正确的手机号"
});
} else {
var sex = "";
if (this.selected == 0) {
sex = "男";
} else if (this.selected == 1) {
sex = "女";
} else {
sex = "男";
}
console.log(this.age);
this.$router.push({
path: "homeConsult",
query: {
name: this.name,
age: this.age,
phone: this.phone,
sex: sex,
doctorId: this.doctorId
}
});
}
},


getUserInfo() {
this.$vux.loading.show({
text: "Loading"
});
base
.get("/api/auth/user/profile/")
.then(res => {
console.log(res, "获取用户信息");
this.$vux.loading.hide();
if (res.data.status == 201002) {
this.name = res.data.msg.nickname;


if (res.data.msg.birthday === "") {
this.age = 0;
} else {
this.age =
new Date().getFullYear() - res.data.msg.birthday.slice(0, 4);
}
if (res.data.msg.sex == "男") {
this.selected = 0;
} else if (res.data.msg.sex == "女") {
this.selected = 1;
} else {
this.selected = 0;
}
if (res.data.msg.user.phone == "") {
this.phone = "未填写";
} else {
this.phone = res.data.msg.user.phone;
}
} else {
console.log("获取用户信息失败");
}
})
.catch(err => {
console.log(err);
this.$vux.loading.hide();
});
}
},
mounted() {
this.doctorId = this.$route.query.doctorId;
this.getUserInfo();













},
components: {
Flexbox,
FlexboxItem
}
};



