import base from "../../components/request/baseUrl.js";
import { setTimeout } from "timers";
export default {
  data() {
    return {
      checked: true,
      name: "",
      idCard: ""
    };
  },
  methods: {
    back() {
      this.$router.push({
        path: "home"
      });
    },

    // 点击右侧小图标进行删除
    delName() {
      this.name = "";
    },

    //点击完成按钮
    submitBtn() {
      var idCardReg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
      if (this.name == "" || this.name.length > 20) {
        this.$vux.toast.show({
          type: "text",
          text: "姓名不能为空且不超过20个字"
        });
      } else if (!idCardReg.test(this.idCard)) {
        this.$vux.toast.show({
          type: "text",
          text: "请输入正确的身份证号"
        });
      } else {
        this.$vux.loading.show({
          text: "Loading"
        });
        base
          .post("/api/auth/user/real_name/", {
            name: this.name,
            id_number: this.idCard
          })
          .then(res => {
            this.$vux.loading.hide();
            console.log(res);
            if (res.data.status == 201005) {
              this.$vux.toast.show({
                text: "实名认证成功"
              });
              setTimeout(() => {
                this.$router.push({
                  path: "details"
                });
              }, 1000);
            } else {
              this.$vux.toast.show({
                type: "text",
                text: res.data.desc
              });
            }
          })
          .catch(err => {
            this.$vux.loading.hide();
            console.log(err);
          });
      }
    }
  }
};



